import React, { useEffect, useState } from 'react'
import { Menu } from 'antd'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import Icon from '../../../components/light/Icon'
import Logo from './Logo'

const { SubMenu } = Menu

function Sidebar(props) {
  const {
    router,
    location: { pathname },
    theme,
    selectedKeys,
    collapsed,
  } = props

  const [openKeys, setOpenKeys] = useState([])

  const defaultOpenKeysFn = () => {
    const arr = []
    let myPathname = pathname.replace(/(\/+$)/g, '')
    let arrTemp = myPathname.split('/')
    if (arrTemp.length > 4) {
      for (let i = 4; i < arrTemp.length; i++) {
        arr.push(arrTemp.slice(0, i).join('/'))
      }
    }
    return arr
  }

  const handleNav = (path) => {
    //props.onSetState(['selectedKeys'], path)
    props.history.push(path)
  }

  const sidebarRender = (arr) => {
    return arr.map((item) => {
      if (item.children) {
        return (
          <SubMenu
            key={item.key}
            title={item.title}
            className={item.className ? item.className : ''}
            icon={
              item.icon ? (
                <span className="anticon anticon-appstore m-sidebar-icon-wrap">
                  <Icon
                    name={item.icon}
                    className={`m-sidebar-icon ${
                      collapsed && (item.key.split('/').length === 4) ? 'big' : ''
                    }`}
                  ></Icon>
                </span>
              ) : null
            }
          >
            {sidebarRender(item.children)}
          </SubMenu>
        )
      } else {
        return (
          <Menu.Item
            key={item.path}
            className={item.className ? item.className : ''}
            onClick={() => handleNav(item.path)}
            icon={
              item.icon ? (
                <span className="anticon anticon-appstore m-sidebar-icon-wrap">
                  <Icon
                    name={item.icon}
                    className={`m-sidebar-icon ${
                      collapsed ? 'big' : ''
                    }`}
                  ></Icon>
                </span>
              ) : null
            }
          >
            <span>{item.title}</span>
          </Menu.Item>
        )
      }
    })
  }

  const sidebarDom = sidebarRender(router)

  const handleOpenChange = (openKeys) => {
    setOpenKeys(openKeys)
  }

  useEffect(() => {
    props.onSetState(['selectedKeys'], pathname)
    // eslint-disable-next-line
  }, [pathname])

  useEffect(() => {
    if(!collapsed) {
      setOpenKeys(defaultOpenKeysFn())
    }
    // eslint-disable-next-line
  }, [collapsed])

  return (
    <div
      style={{ width: collapsed ? 80 : 256 }}
    >
      <Menu
        selectedKeys={[selectedKeys]}
        openKeys={openKeys}
        style={{ height: '100%', overflowY: 'auto' }}
        mode="inline"
        theme={theme}
        inlineCollapsed={collapsed}
        onOpenChange={handleOpenChange}
      >
        <Logo collapsed={collapsed}></Logo>
        {sidebarDom}
      </Menu>
    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    router: state.getIn(['light', 'router']).toJS(),
    theme: state.getIn(['light', 'theme']),
    selectedKeys: state.getIn(['light', 'selectedKeys']),
    collapsed: state.getIn(['light', 'collapsed']),
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onSetState(key, value) {
      dispatch({ type: 'SET_LIGHT_STATE', key, value })
    },
    onDispatch(action) {
      dispatch(action)
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Sidebar))
