import Api from '../../api'

const getList = () => (dispatch) => {
  Api.list().then(res => {
    if (res.code === 200) {
      dispatch({ type: 'SET_STATE', key: ['list'], value: res.data })
    }
  })
}

const getUserInfo = () => (dispatch) => {
  Api.light.getUserInfo().then(res => {
    if (res.code === 200) {
      dispatch({ type: 'SET_LIGHT_STATE', key: ['userInfo'], value: res.data })
    }
  })
}

export {
  getList,
  getUserInfo
}